import React from 'react';
import {
  Box,
  Heading,
  Text,
  Button,
  VStack,
  HStack,
  Badge,
  Divider,
  useColorModeValue
} from '@chakra-ui/react';

const UserProfile = ({ account, logoutWallet }) => {
  const bgColor = useColorModeValue('white', 'gray.700');
  const borderColor = useColorModeValue('gray.200', 'gray.600');

  if (!account) return null;

  return (
    <Box
      id="user-profile"
      p={6}
      borderWidth="1px"
      borderRadius="lg"
      bg={bgColor}
      borderColor={borderColor}
      shadow="sm"
    >
      <VStack spacing={4} align="stretch">
        <Heading size="md" color="brand.600">User Profile</Heading>
        <Divider />
        
        <Box>
          <Text fontSize="sm" color="gray.500">Connected Wallet</Text>
          <HStack spacing={2} mt={1}>
            <Badge colorScheme="green" px={2} py={1} borderRadius="full">Active</Badge>
            <Text fontWeight="medium" className="wallet-address">
              {`${account.substring(0, 6)}...${account.substring(38)}`}
            </Text>
          </HStack>
        </Box>
        
        <Box>
          <Text fontSize="sm" color="gray.500">Collection Stats</Text>
          <HStack spacing={4} mt={1}>
            <Box>
              <Text fontSize="sm">Owned NFTs</Text>
              <Text fontWeight="bold">2</Text>
            </Box>
            <Box>
              <Text fontSize="sm">Listed NFTs</Text>
              <Text fontWeight="bold">1</Text>
            </Box>
          </HStack>
        </Box>
        
        <Box pt={2}>
          <Button 
            colorScheme="red" 
            variant="outline" 
            size="sm" 
            width="full"
            onClick={logoutWallet}
          >
            Disconnect Wallet
          </Button>
        </Box>
      </VStack>
    </Box>
  );
};

export default UserProfile; 