import React from 'react';
import { 
  Box, 
  Flex, 
  Heading, 
  Spacer, 
  Button, 
  HStack,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  useColorModeValue
} from '@chakra-ui/react';
import { ChevronDownIcon } from '@chakra-ui/icons';

const MarketplaceHeader = ({ account, connectWallet, logoutWallet }) => {
  const bgColor = useColorModeValue('white', 'gray.800');
  const borderColor = useColorModeValue('gray.200', 'gray.700');

  return (
    <Box 
      as="header" 
      py={4} 
      px={8} 
      bg={bgColor} 
      borderBottom="1px" 
      borderBottomColor={borderColor} 
      boxShadow="sm"
    >
      <Flex align="center" maxW="container.xl" mx="auto">
        <Heading size="lg" color="brand.600">Kuverse NFT</Heading>
        
        <Spacer />
        
        <HStack spacing={4}>
          {account ? (
            <Menu>
              <MenuButton 
                as={Button} 
                variant="outline" 
                colorScheme="brand"
                rightIcon={<ChevronDownIcon />}
              >
                {`${account.substring(0, 6)}...${account.substring(38)}`}
              </MenuButton>
              <MenuList>
                <MenuItem 
                  fontWeight="medium" 
                  color="red.500" 
                  onClick={logoutWallet}
                >
                  Disconnect Wallet
                </MenuItem>
              </MenuList>
            </Menu>
          ) : (
            <Button 
              colorScheme="brand" 
              onClick={connectWallet}
            >
              Connect Wallet
            </Button>
          )}
        </HStack>
      </Flex>
    </Box>
  );
};

export default MarketplaceHeader; 