import { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Heading,
  Tab,
  TabList,
  TabPanel,
  TabPanels,
  Tabs,
  VStack,
  Text,
  useToast
} from '@chakra-ui/react';
import { ethers } from 'ethers';
import Web3Modal from 'web3modal';
import MarketplaceHeader from './components/MarketplaceHeader';
import ConnectWallet from './components/ConnectWallet';
import MintNFTForm from './components/MintNFTForm';
import NFTCard from './components/NFTCard';
import UserProfile from './components/UserProfile';
import { NFT_CONTRACT_ADDRESS, MARKETPLACE_CONTRACT_ADDRESS, KuverseNFTABI, KuverseMarketplaceABI } from './contracts/contractInfo';

function App() {
  const [account, setAccount] = useState(null);
  const [nfts, setNfts] = useState([]);
  const [listedNfts, setListedNfts] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const toast = useToast();

  // Check for stored wallet connection on component mount
  useEffect(() => {
    const storedAddress = localStorage.getItem('walletAddress');
    if (storedAddress) {
      reconnectWallet(storedAddress);
    }
  }, []);

  const reconnectWallet = async (address) => {
    try {
      setIsLoading(true);
      
      // Attempt to reconnect with stored address
      const web3Modal = new Web3Modal({
        cacheProvider: true // Use this to enable session caching
      });
      
      // Check if cached provider exists
      if (web3Modal.cachedProvider) {
        const connection = await web3Modal.connect();
        const provider = new ethers.providers.Web3Provider(connection);
        const signer = provider.getSigner();
        const currentAddress = await signer.getAddress();
        
        // Verify the address matches the stored one
        if (currentAddress.toLowerCase() === address.toLowerCase()) {
          setAccount(currentAddress);
          
          // Setup listeners for account changes or disconnect
          provider.provider.on("accountsChanged", handleAccountsChanged);
          provider.provider.on("disconnect", handleDisconnect);
          
          // Load mock NFT data for demo
          loadMockNFTData();
          
          toast({
            title: 'Wallet Reconnected',
            description: `Connected to ${currentAddress.substring(0, 6)}...${currentAddress.substring(38)}`,
            status: 'success',
            duration: 5000,
            isClosable: true,
          });
        }
      }
    } catch (error) {
      console.error('Error reconnecting wallet:', error);
      // If reconnection fails, clear stored data
      localStorage.removeItem('walletAddress');
    } finally {
      setIsLoading(false);
    }
  };
  
  const handleAccountsChanged = (accounts) => {
    if (accounts.length === 0) {
      // User disconnected their wallet
      logoutWallet();
    } else {
      // User switched accounts
      const newAccount = accounts[0];
      setAccount(newAccount);
      localStorage.setItem('walletAddress', newAccount);
      
      toast({
        title: 'Account Changed',
        description: `Switched to ${newAccount.substring(0, 6)}...${newAccount.substring(38)}`,
        status: 'info',
        duration: 5000,
        isClosable: true,
      });
    }
  };
  
  const handleDisconnect = () => {
    logoutWallet();
  };
  
  const logoutWallet = () => {
    // Clear storage
    localStorage.removeItem('walletAddress');
    
    // Clear state
    setAccount(null);
    setNfts([]);
    setListedNfts([]);
    
    // Clear web3modal cachedProvider
    const web3Modal = new Web3Modal();
    if (web3Modal.cachedProvider) {
      web3Modal.clearCachedProvider();
    }
    
    toast({
      title: 'Wallet Disconnected',
      description: 'Your wallet has been disconnected',
      status: 'info',
      duration: 5000,
      isClosable: true,
    });
  };
  
  // Helper function to load mock NFT data
  const loadMockNFTData = () => {
    // For demo purposes, create some mock NFT data
    const mockNfts = [
      {
        id: '1',
        name: 'Cosmic Explorer',
        description: 'A journey through the cosmic wonders',
        image: 'https://ipfs.io/ipfs/QmNQVTJLDPfjLQtyZLp3RhytUx1qadLxHnNn9Arg7xdv6o',
        isListed: false
      },
      {
        id: '2',
        name: 'Digital Dream',
        description: 'Exploration of digital consciousness',
        image: 'https://ipfs.io/ipfs/QmXkGaTAFyKEQbX4YQMiKTpDQvYGcuW8YKQEMJVsnpCLtk',
        isListed: false
      }
    ];
    
    const mockListedNfts = [
      {
        id: '3',
        name: 'Ethereal Vision',
        description: 'A glimpse into the ethereal realm',
        image: 'https://ipfs.io/ipfs/QmYxT4LnK8bbLyS2JhvxuqLaTbpK83jVvWvzQRh1vxgSQj',
        price: ethers.utils.parseEther('0.15'),
        isListed: true
      },
      {
        id: '4',
        name: 'Quantum Artifact',
        description: 'Relic from a quantum dimension',
        image: 'https://ipfs.io/ipfs/QmbmBH4Y8YVcKqQYeQEzxmF1k7MU6a6UXdPBN1XrC5Nxp2',
        price: ethers.utils.parseEther('0.25'),
        isListed: true
      }
    ];
    
    setNfts(mockNfts);
    setListedNfts(mockListedNfts);
  };

  const connectWallet = async () => {
    try {
      setIsLoading(true);
      const web3Modal = new Web3Modal({
        cacheProvider: true // Enable session caching
      });
      const connection = await web3Modal.connect();
      const provider = new ethers.providers.Web3Provider(connection);
      const signer = provider.getSigner();
      const address = await signer.getAddress();
      
      // Set up event listeners for wallet changes/disconnects
      provider.provider.on("accountsChanged", handleAccountsChanged);
      provider.provider.on("disconnect", handleDisconnect);
      
      // Store wallet address in state
      setAccount(address);
      
      // Save to localStorage for persistence across page reloads
      localStorage.setItem('walletAddress', address);
      
      // Load mock NFT data
      loadMockNFTData();
      
      toast({
        title: 'Wallet Connected',
        description: `Connected to ${address.substring(0, 6)}...${address.substring(38)}`,
        status: 'success',
        duration: 5000,
        isClosable: true,
      });
    } catch (error) {
      console.error('Error connecting wallet:', error);
      toast({
        title: 'Connection Failed',
        description: 'Failed to connect wallet. Please try again.',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    } finally {
      setIsLoading(false);
    }
  };

  const loadNFTs = async () => {
    // In a real implementation, this would load NFTs from the blockchain
    console.log('Loading NFTs - this is a placeholder for real implementation');
  };

  const mintNFT = async (name, description, imageUrl) => {
    try {
      setIsLoading(true);
      
      // In a real implementation, this would call the contract
      // For demo, we'll just add to the local state
      const newNft = {
        id: `${Date.now()}`,
        name,
        description,
        image: imageUrl,
        isListed: false
      };
      
      setNfts([...nfts, newNft]);
      
      toast({
        title: 'NFT Minted',
        description: `Successfully minted "${name}"`,
        status: 'success',
        duration: 5000,
        isClosable: true,
      });
    } catch (error) {
      console.error('Error minting NFT:', error);
      toast({
        title: 'Minting Failed',
        description: 'Failed to mint NFT. Please try again.',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    } finally {
      setIsLoading(false);
    }
  };

  const listNFT = async (tokenId, price) => {
    try {
      setIsLoading(true);
      
      // In a real implementation, this would call the contract
      // For demo, we'll just update the local state
      const nftToList = nfts.find(nft => nft.id === tokenId);
      if (nftToList) {
        const updatedNft = {
          ...nftToList,
          isListed: true,
          price: ethers.utils.parseEther(price.toString())
        };
        
        setNfts(nfts.filter(nft => nft.id !== tokenId));
        setListedNfts([...listedNfts, updatedNft]);
        
        toast({
          title: 'NFT Listed',
          description: `"${nftToList.name}" listed for ${price} ETH`,
          status: 'success',
          duration: 5000,
          isClosable: true,
        });
      }
    } catch (error) {
      console.error('Error listing NFT:', error);
      toast({
        title: 'Listing Failed',
        description: 'Failed to list NFT. Please try again.',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    } finally {
      setIsLoading(false);
    }
  };

  const buyNFT = async (tokenId) => {
    try {
      setIsLoading(true);
      
      // In a real implementation, this would call the contract
      // For demo, we'll just update the local state
      const nftToBuy = listedNfts.find(nft => nft.id === tokenId);
      if (nftToBuy) {
        const boughtNft = {
          ...nftToBuy,
          isListed: false
        };
        
        setListedNfts(listedNfts.filter(nft => nft.id !== tokenId));
        setNfts([...nfts, boughtNft]);
        
        toast({
          title: 'NFT Purchased',
          description: `Successfully purchased "${nftToBuy.name}"`,
          status: 'success',
          duration: 5000,
          isClosable: true,
        });
      }
    } catch (error) {
      console.error('Error buying NFT:', error);
      toast({
        title: 'Purchase Failed',
        description: 'Failed to buy NFT. Please try again.',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Box minH="100vh" bg="gray.50">
      <MarketplaceHeader account={account} connectWallet={connectWallet} logoutWallet={logoutWallet} />
      
      <Container maxW="container.xl" py={8}>
        {!account ? (
          <VStack spacing={8} py={16} align="center">
            <Heading size="xl" textAlign="center" color="brand.600">
              Welcome to Kuverse NFT Marketplace
            </Heading>
            <Text fontSize="lg" textAlign="center" maxW="container.md" color="gray.600">
              Connect your wallet to mint, list, and trade NFTs on the Kuverse platform.
            </Text>
            <ConnectWallet connectWallet={connectWallet} isLoading={isLoading} />
          </VStack>
        ) : (
          <Tabs colorScheme="brand" variant="soft-rounded">
            <TabList mb={4}>
              <Tab>Mint a New NFT</Tab>
              <Tab>My NFTs ({nfts.length})</Tab>
              <Tab>NFT Marketplace ({listedNfts.length})</Tab>
            </TabList>
            
            <TabPanels>
              <TabPanel>
                <Box display="grid" gridTemplateColumns={{ base: "1fr", md: "3fr 1fr" }} gap={8}>
                  <MintNFTForm mintNFT={mintNFT} isLoading={isLoading} />
                  <UserProfile account={account} logoutWallet={logoutWallet} />
                </Box>
              </TabPanel>
              
              <TabPanel>
                {nfts.length === 0 ? (
                  <Text textAlign="center" py={10} color="gray.500">
                    You don't own any NFTs yet. Mint one or buy from the marketplace!
                  </Text>
                ) : (
                  <Box display="grid" gridTemplateColumns={{ base: "1fr", md: "3fr 1fr" }} gap={8}>
                    <Box display="grid" gridTemplateColumns="repeat(auto-fill, minmax(280px, 1fr))" gap={6}>
                      {nfts.map(nft => (
                        <NFTCard
                          key={nft.id}
                          nft={{
                            id: nft.id,
                            name: nft.name,
                            description: nft.description,
                            image: nft.image,
                            isListed: nft.isListed
                          }}
                          isMine={true}
                          onListNFT={listNFT}
                          isLoading={isLoading}
                        />
                      ))}
                    </Box>
                    <UserProfile account={account} logoutWallet={logoutWallet} />
                  </Box>
                )}
              </TabPanel>
              
              <TabPanel>
                {listedNfts.length === 0 ? (
                  <Text textAlign="center" py={10} color="gray.500">
                    There are no NFTs listed in the marketplace yet.
                  </Text>
                ) : (
                  <Box display="grid" gridTemplateColumns="repeat(auto-fill, minmax(280px, 1fr))" gap={6}>
                    {listedNfts.map(nft => (
                      <NFTCard
                        key={nft.id}
                        nft={{
                          id: nft.id,
                          name: nft.name,
                          description: nft.description,
                          image: nft.image,
                          isListed: nft.isListed,
                          price: nft.price
                        }}
                        isMine={false}
                        onBuyNFT={buyNFT}
                        isLoading={isLoading}
                      />
                    ))}
                  </Box>
                )}
              </TabPanel>
            </TabPanels>
          </Tabs>
        )}
      </Container>
    </Box>
  );
}

export default App; 