import React, { useState } from 'react';
import {
  Box,
  Image,
  Text,
  Button,
  VStack,
  HStack,
  Badge,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalCloseButton,
  FormControl,
  FormLabel,
  NumberInput,
  NumberInputField,
  NumberInputStepper,
  NumberIncrementStepper,
  NumberDecrementStepper,
  useDisclosure,
  useColorModeValue,
  Flex
} from '@chakra-ui/react';
import { ethers } from 'ethers';

const NFTCard = ({ nft, isMine, onBuyNFT, onListNFT, isLoading }) => {
  const { isOpen: isListModalOpen, onOpen: onListModalOpen, onClose: onListModalClose } = useDisclosure();
  const [listingPrice, setListingPrice] = useState('0.1');
  
  const bgColor = useColorModeValue('white', 'gray.700');
  const textColor = useColorModeValue('gray.800', 'white');
  const borderColor = useColorModeValue('gray.200', 'gray.600');
  
  const handleList = () => {
    onListNFT(nft.id, parseFloat(listingPrice));
    onListModalClose();
  };
  
  const handleBuy = () => {
    onBuyNFT(nft.id, nft.price);
  };
  
  const formattedPrice = nft.isListed && nft.price ? 
    `${ethers.utils.formatEther(nft.price)} ETH` : 
    null;
    
  return (
    <>
      <Box
        borderWidth="1px"
        borderRadius="lg"
        overflow="hidden"
        boxShadow="sm"
        bg={bgColor}
        borderColor={borderColor}
        transition="all 0.2s"
        _hover={{ transform: 'translateY(-2px)', boxShadow: 'md' }}
      >
        <Box position="relative">
          <Image
            src={nft.image}
            alt={nft.name}
            objectFit="cover"
            width="100%"
            height="220px"
          />
          {nft.isListed && (
            <Badge
              position="absolute"
              top="10px"
              right="10px"
              colorScheme="green"
              borderRadius="full"
              px={2}
            >
              For Sale
            </Badge>
          )}
        </Box>
        
        <VStack p={4} spacing={3} align="stretch">
          <Text fontWeight="bold" fontSize="lg" noOfLines={1} color={textColor}>
            {nft.name}
          </Text>
          
          <Text fontSize="sm" color="gray.500" noOfLines={2}>
            {nft.description}
          </Text>
          
          {nft.isListed && formattedPrice && (
            <HStack>
              <Text fontWeight="semibold" color="brand.600">
                Price:
              </Text>
              <Text>{formattedPrice}</Text>
            </HStack>
          )}
          
          <Flex mt={2}>
            {isMine ? (
              nft.isListed ? (
                <Button size="sm" colorScheme="red" variant="outline" isFullWidth>
                  Cancel Listing
                </Button>
              ) : (
                <Button
                  size="sm"
                  colorScheme="brand"
                  onClick={onListModalOpen}
                  isFullWidth
                >
                  List for Sale
                </Button>
              )
            ) : (
              nft.isListed && (
                <Button
                  size="sm"
                  colorScheme="brand"
                  onClick={handleBuy}
                  isLoading={isLoading}
                  loadingText="Buying..."
                  isFullWidth
                >
                  Buy Now
                </Button>
              )
            )}
          </Flex>
        </VStack>
      </Box>
      
      {/* List NFT Modal */}
      <Modal isOpen={isListModalOpen} onClose={onListModalClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>List {nft.name} for Sale</ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}>
            <FormControl>
              <FormLabel>Price in ETH</FormLabel>
              <NumberInput
                min={0.01}
                step={0.01}
                precision={2}
                value={listingPrice}
                onChange={(value) => setListingPrice(value)}
              >
                <NumberInputField />
                <NumberInputStepper>
                  <NumberIncrementStepper />
                  <NumberDecrementStepper />
                </NumberInputStepper>
              </NumberInput>
            </FormControl>
            
            <Button
              mt={6}
              colorScheme="brand"
              onClick={handleList}
              isLoading={isLoading}
              loadingText="Listing..."
              w="100%"
            >
              List NFT
            </Button>
          </ModalBody>
        </ModalContent>
      </Modal>
    </>
  );
};

export default NFTCard; 