import React, { useState } from 'react';
import {
  FormControl,
  FormLabel,
  Input,
  Textarea,
  Button,
  VStack,
  HStack,
  Box,
  Text,
  SimpleGrid,
  Image,
  useColorModeValue
} from '@chakra-ui/react';

const MintNFTForm = ({ mintNFT, isLoading }) => {
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [previewVisible, setPreviewVisible] = useState(false);

  const inputBgColor = useColorModeValue('white', 'gray.700');
  const borderColor = useColorModeValue('gray.200', 'gray.600');

  const handleSubmit = (e) => {
    e.preventDefault();
    mintNFT(name, description, imageUrl);
  };

  const handlePreview = () => {
    if (name && imageUrl) {
      setPreviewVisible(true);
    }
  };

  const isFormValid = name && description && imageUrl;

  return (
    <Box>
      <SimpleGrid columns={{ base: 1, md: 2 }} spacing={8}>
        <VStack as="form" spacing={6} onSubmit={handleSubmit} align="stretch">
          <FormControl isRequired>
            <FormLabel>NFT Name</FormLabel>
            <Input
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="Enter a name for your NFT"
              bg={inputBgColor}
              borderColor={borderColor}
            />
          </FormControl>

          <FormControl isRequired>
            <FormLabel>Description</FormLabel>
            <Textarea
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="Describe your NFT"
              bg={inputBgColor}
              borderColor={borderColor}
              rows={3}
            />
          </FormControl>

          <FormControl isRequired>
            <FormLabel>Image URL</FormLabel>
            <Input
              value={imageUrl}
              onChange={(e) => setImageUrl(e.target.value)}
              placeholder="Enter URL to your NFT image (e.g., IPFS URL)"
              bg={inputBgColor}
              borderColor={borderColor}
            />
          </FormControl>

          <HStack spacing={4}>
            <Button 
              colorScheme="brand" 
              type="submit" 
              isDisabled={!isFormValid}
              isLoading={isLoading}
              loadingText="Minting..."
              flex="1"
            >
              Mint NFT
            </Button>
            <Button 
              variant="outline" 
              onClick={handlePreview} 
              isDisabled={!name || !imageUrl}
              flex="1"
            >
              Preview
            </Button>
          </HStack>
        </VStack>

        {previewVisible && (
          <Box 
            p={6} 
            borderRadius="xl" 
            borderWidth="1px" 
            borderColor={borderColor}
            bg={inputBgColor}
            boxShadow="sm"
          >
            <VStack spacing={4} align="stretch">
              <Text fontWeight="bold" fontSize="lg">Preview</Text>
              <Box 
                borderRadius="lg" 
                overflow="hidden" 
                borderWidth="1px" 
                borderColor={borderColor}
              >
                <Image 
                  src={imageUrl} 
                  alt={name} 
                  fallback={<Box h="200px" bg="gray.100" />}
                />
              </Box>
              <Text fontWeight="bold">{name}</Text>
              <Text noOfLines={4}>{description}</Text>
            </VStack>
          </Box>
        )}
      </SimpleGrid>
    </Box>
  );
};

export default MintNFTForm; 